import React, { Component } from 'react'
import './App.css';
import uuid from'uuid';
import TodoApp from './todo list/TodoApp';
import About from './about/About';
import Contact from './contact/Contact';
import Header from './header/Header';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import AddTodo from './todo list/AddTodo';

export class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      list: [
        {
          id: uuid.v4(),
          text: "this is my first text",
          completed: false,
          category: []
        },
        {
          id: uuid.v4(),
          text: "this is my second text",
          completed: false,
          category: []
        },
        {
          id: uuid.v4(),
          text: "this is my 3rd text",
          completed: false,
          category: []
        }
      ]
    }
    this.addList = (text) => {
      const newTodo = {
        id: uuid.v4(),
        text: text,
        completed: false,
        category: []
      }
      if(text) {
        this.setState({list: [...this.state.list, newTodo]})
      }
    }
    this.markComplete = (id) => {
      this.setState({list: this.state.list.map( item => {
        if(item.id===id){
          item.completed = !item.completed;
        }
        return item
      })})
    }
    this.delList = (id) => {
      this.setState({ list: [...this.state.list.filter( list => list.id !== id)] })
    }
    this.addCat = (cat,id) => {
      const catsss = this.state.list.map(item => {
        if(item.id === id) {
          item.category.push(cat);
        }
        return item;
      })
      this.setState({list: catsss})
    }
  }

  render() {
    return (
      <Router>
        <div className="App">
          <Header />
            <Route exact path="/todo-app" render={props => (
              <React.Fragment>
                <AddTodo addList={this.addList}/>
                <TodoApp list={this.state.list} delList={this.delList} addCat={this.addCat} markComplete={this.markComplete} />
              </React.Fragment>
            )} />
            <Route path="/about" component={About} />
            <Route path="/contact" component={Contact} />
        </div>
      </Router>
    )
  }
}


export default App;
